import React from 'react'
import {scroller} from 'react-scroll'
import '../styles/Service.css'

const scrollToContact = () => () => {
	scroller.scrollTo('contact', {duration: 1500, delay: 100, smooth: true})
}

const Service = () =>
	<div className="Service">
		<div className="Service-arbo">
			<div className="Service-box">
				<h1>Arboristika</h1>
        <div>
          Varboristice provdme vechny prce od rizikovho kcen, pes odborn oetovn strom a zajiovn
          strom, proezvky ovocnch strom i likvidace devn hmoty.
        </div>
			</div>
		</div>
    <div className="Service-work">
      <div className="Service-box">
        <h1>Vkov prce</h1>
        <div>
          Ve vkovch pracch pokrvme podstat vechna povoln. Jedn se pedevm o opravy fasd, kompletn servis
          proti ptactvu, drobn klempsk a pokrvask opravy, tlakov myt fasd, myt oken, opravy spr panelovch
          dom a jin.
        </div>
        <div className="Service-go-to-contact">
          <button onClick={scrollToContact()}>
            Kontakt
          </button>
        </div>
      </div>
    </div>
	</div>

export default Service


